import * as React from "react";
import saveApp, { useUserInfo } from "../../components/save";
import Defer from "../../components/ui/Defer";
import ajax, { useFetcher } from '../../components/ajax';
import Navbar from "../../components/ui/Navbar";
import { Breadcrumbs, Button, FormControl, List, ListItem, ListItemSecondaryAction, ListItemText, Select, Typography } from "@material-ui/core";
import { Link, navigate } from "gatsby";

const Page = () => {
    const user = useUserInfo();
    const [prov, setProv] = React.useState(null);
    const [kab, setKab] = React.useState(null);
    const [kec, setKec] = React.useState(null);
    const [desa, setDesa] = React.useState(null);
    const [lprov, lkab, lkec, ldesa] = [
        useFetcher('/desa/geolookup'),
        useFetcher(prov && '/desa/geolookup?id=' + prov),
        useFetcher(kab && '/desa/geolookup?id=' + kab),
        useFetcher(kec && '/desa/geolookup?id=' + kec),
    ];
    console.log(lprov);
    React.useEffect(() => {
        if (user) {
            var geo = user.account.geocode;
            if (!geo)
                geo = user.nik.substr(0, 6);
            setProv(geo.substr(0, 2));
            setKab(geo.substr(0, 4));
            setKec(geo.substr(0, 6));
            setDesa(geo);
        }
    }, [user]);
    const save = ((e) => {
        e.preventDefault();
        if (!desa) return;
        ajax.post('/auth/info', {
            "geocode": desa,
        }).then(() => {
            alert('Berhasil disimpan');
            saveApp.userInfo = null;
            navigate('/user/profile/');
        });
    });
    return (
        <Defer data={user}>
            {() => (
                <form onSubmit={save}>
                    <Navbar />
                    <List component="nav">
                        <ListItem>
                            <Breadcrumbs>
                                <Link color="inherit" to="/user/profile/">
                                    Pengaturan Akun
                                </Link>
                                <Typography color="textPrimary">Atur Lokasi Desa</Typography>
                            </Breadcrumbs>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Provinsi" />
                            <ListItemSecondaryAction>
                                <FormControl disabled={!lprov} style={{ minWidth: '50vw' }}>
                                    <Select native value={prov} onChange={(x) => { setProv(x.target.value); setKab(null); setKec(null); setDesa(null) }}>
                                        {lprov ? lprov.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                    </Select>
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Kabupaten" />
                            <ListItemSecondaryAction>
                                <FormControl disabled={!lkab} style={{ minWidth: '50vw' }}>
                                    <Select native value={kab} onChange={(x) => { setKab(x.target.value); setKec(null); setDesa(null) }}>
                                        {lkab ? lkab.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                    </Select>
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Kecamatan" />
                            <ListItemSecondaryAction>
                                <FormControl disabled={!lkec} style={{ minWidth: '50vw' }}>
                                    <Select native value={kec} onChange={(x) => { setKec(x.target.value); setDesa(null) }}>
                                        {lkec ? lkec.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                    </Select>
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Desa" />
                            <ListItemSecondaryAction>
                                <FormControl disabled={!ldesa} style={{ minWidth: '50vw' }}>
                                    <Select native value={desa} onChange={(x) => { setDesa(x.target.value) }}>
                                        {ldesa ? ldesa.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                    </Select>
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <Button type='submit' variant="contained" color="primary" disabled={!desa}>
                                Simpan
                            </Button>
                        </ListItem>
                    </List>
                </form>
            )}
        </Defer>
    );
}

export default Page;
